.popup__content {    
    position: relative;
    background-color: rgba(255, 255, 255, 1);
    border-radius: 10px;
    box-shadow: 0px 0 25px rgba(0, 0, 0, 15%);
    max-width: 430px;
    width: 100%;
}

@media (max-width: 320px) {
    .popup__content {
        max-width: 282px;
    }
}