.login__button {
    max-width: 358px;
    width: 100%;
    min-height: 50px;
    background-color: #fff;
    border-radius: 2px;    
    border: none;
    color: black;
    padding: 0;
    box-sizing: border-box;
    font-size: 18px;
    font-weight: 400;
    line-height: 122%;
    cursor: pointer;
    margin: 186px auto 0;
    display:block;
}

.login__button:hover {
    opacity: .6;
}

@media (max-width: 375px) {
    .login__button {
        max-width: 260px;
        width: 100%;
        font-size: 16px;
        line-height: 1.18;
    }    
}