.profile {
    max-width: 880px;
    margin: 40px auto 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

@media (max-width: 639px) {
    .profile {
        max-width: 450px;
        margin: 42px auto 0;
        flex-direction: column;
    }
}